  width: 50%;
}
.timeline-item .icon {
  position: absolute;
  display: inline-flex;
  width: 25px;
  height: 25px;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  top: 30px;
  right: -15px;
  padding: 9px 0;
  background: var(--tb-card-bg);
  border: 1px solid #cddcea;
  border-radius: 50px;
  color: #2dcb73;
  z-index: 1;
}
.timeline-item .date {
  position: absolute;
  display: inline-block;
  width: calc(100% - 48px);
  top: 50px;
  font-size: 14px;
  font-weight: 500;
  font-style: italic;
}
.timeline-item .content {
  padding: 20px;
  background: var(--tb-card-bg-custom);
  position: relative;
  border: 1px solid var(--tb-border-color);
  border-radius: 0.3rem;
  box-shadow: var(--tb-shadow-sm);
  border-right: 25px solid rgba(90, 88, 235, 0.5);
  position: relative;
}
.timeline-item .content:before {
  content: "";
  position: absolute;
  right: -45px;
  top: 13%;
  border-top: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid rgba(90, 88, 235, 0.5);
}
.timeline-item:nth-child(even) .content {
  border-right: 1px solid var(--tb-border-color);
  border-left: 25px solid rgba(90, 88, 235, 0.5);
}
.timeline-item:nth-child(even) .content:before {
  right: auto;
  left: -45px;
  border-left: 10px solid transparent;
  border-right: 10px solid rgba(90, 88, 235, 0.5);
}
.timeline-item:nth-child(5n-1) .content {
  border-left-color: rgba(249, 85, 76, 0.5) !important;
}
.timeline-item:nth-child(5n-1) .content:before {
  border-right-color: rgba(249, 85, 76, 0.5) !important;
}
.timeline-item:nth-child(5n-2) .content {
  border-right-color: rgba(67, 142, 255, 0.5) !important;
}
.timeline-item:nth-child(5n-2) .content:before {
  border-left-color: rgba(67, 142, 255, 0.5) !important;
}
.timeline-item:nth-child(3) .content:before {
  border-right-color: transparent;
  border-left-color: rgba(67, 142, 255, 0.5) !important;
}
.timeline-item:nth-child(5n-3) .content {
  border-left-color: rgba(23, 159, 170, 0.5) !important;
}
.timeline-item:nth-child(5n-3) .content:before {
  border-right-color: rgba(23, 159, 170, 0.5) !important;
}
.timeline-item:nth-child(5n-4) .content {
  border-right-color: rgba(233, 188, 24, 0.5) !important;
}
.timeline-item:nth-child(5n-4) .content:before {
  border-left-color: rgba(233, 188, 24, 0.5) !important;
}
.timeline-item:nth-child(6) .content {
  border-right-color: var(--tb-border-color) !important;
  border-left-color: rgba(233, 188, 24, 0.5) !important;
}
.timeline-item:nth-child(6) .content:before {
  border-right-color: rgba(233, 188, 24, 0.5) !important;
  border-left-color: transparent !important;
}
.timeline-item.left {
  left: 0;
}
.timeline-item.left .date {
  left: calc(100% + 48px);
  text-align: start;
}
.timeline-item.right {
  left: 50%;
}
.timeline-item.right .icon {
  left: -10px;
}
.timeline-item.right .date {
  right: calc(100% + 48px);
  text-align: end;
}
.timeline-item.right::before {
  left: 28px;
  border-color: transparent transparent transparent #fff;
}

@media (max-width: 991.98px) {
  .timeline::after {
    left: 100px;
    bottom: 180px;
  }
  .timeline-item {
    width: 100%;
    padding-left: 165px;
    padding-right: 0px;
  }
  .timeline-item .content {
    border-right: 1px solid #cddcea;
    border-left: 15px solid rgba(90, 88, 235, 0.5);
  }
  .timeline-item .content:before {
    right: auto;
    border: none;
  }
  .timeline-item:nth-child(even) .content {
    border-left: 15px solid;
    border-right: 1px solid #cddcea !important;
  }
  .timeline-item:nth-child(5n-4) .content, .timeline-item:nth-child(5n-2) .content {
    border-right-color: #cddcea !important;
  }
  .timeline-item.right, .timeline-item.left {
    left: 0%;
  }
  .timeline-item.right .icon, .timeline-item.left .icon {
    width: 20px;
    height: 15px;
    top: 37.5px;
    font-size: 10px;
    left: 90px;
  }
  .timeline-item.right::before, .timeline-item.left::before {
    left: 110px;
    border-color: transparent transparent transparent #cddcea;
  }
  .timeline-item.right .date, .timeline-item.left .date {
    top: 38px;
    right: auto;
    left: 0px;
    width: 79px;
  }
}
@media (max-width: 600px) {
  .timeline::after {
    display: none;
  }
  .timeline-item {
    padding-left: 0;
  }
  .timeline-item .icon,
  .timeline-item .date {
    display: none;
  }
